.spinnerLoader {
  width: 100%;
  text-align: center;
  margin-top: 20px;
  img { width: 45px; }
}

.evntDesc {
  display: none;
}

.fc-content {
  overflow: inherit!important;
  position: relative;
  padding: 0!important;
  &:before {
    position: absolute;
    z-index: 9;
    top: 18px;
    right: 2px;
    padding: 2px 10px;
    background-color: #fff;
    color: #000;
    font-size: 12px;
    content: 'Erase'; }
}